<template>
  <div class="mymain" ref="mymian">
    <div class="topbar">
      <div class="left">
        <div class="date">
          <div class="date-day">{{ nowDate }}</div>
          <div class="date-month">
            <span>{{ nowMonth }}</span
            ><span>月</span>
          </div>
        </div>
        <div class="line"></div>
        <div class="togreet">{{ msg }}</div>
      </div>
      <div class="right" @click="myGo">
        <img :src="imgStr" alt="" />
      </div>
    </div>
    <div class="banner">
      <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
        <van-swipe-item
          v-for="(item, index) in Articlelist2"
          :key="index"
          @click="goCont(item)"
        >
          <div class="banImg">
            <img :src="item.image" alt="" />
          </div>
          <div class="cont">
            <p>{{ item.title }}</p>
            <span>{{ item.hint }}&nbsp;</span>
            <span>&nbsp;</span>
          </div>
        </van-swipe-item>
      </van-swipe>
    </div>

    <main>
      <div
        class="list"
        v-for="(item, index) in Articlelist"
        :key="'list' + index"
        @click="goCont(item)"
      >
        <div class="list-cont">
          <p>{{ item.title }}</p>
          <span>{{ item.hint }}</span>
        </div>
        <div class="list-img">
          <img :src="item.images[0]" alt="" />
        </div>
      </div>
      <div
        class="box"
        v-for="(item, index) in historyList"
        :key="'box' + index"
      >
        <div class="split-line" v-if="item.gDate1">
          <!-- v-if="item.gDate1" -->
          <span class="m-date">{{ item.gMonth }}月{{ item.gDate1 }}日</span>
          <span class="m-line"></span>
        </div>

        <div class="list2" @click="goCont(item)">
          <div class="list-cont">
            <p>{{ item.title }}</p>
            <span>{{ item.hint }}</span>
          </div>
          <div class="list-img">
            <img :src="item.images[0]" alt="" />
          </div>
        </div>
      </div>
    </main>
  </div>
</template>
 
 <script>
export default {
  name: "mymain",

  data() {
    return {
      msg: "",
      Articlelist: [],
      Articlelist2: [],
      Articledate: "",
      historyList: [],
      gDate: 0,
      oldDay: 0,
      oneDay: 0,
      oneDayNum: 0,
      naw: Date.now(),
      str: "",
      navstr: {},
      nowDate: "",
      nowMonth: "",
      imgStr: require('../assets/rto.png'),
    };
  },
  // 加载今天的日报
  created() {
    let str = JSON.parse(localStorage.getItem("str")) || [];
    let dater = new Date();
    this.nowDate = dater.getDate();
    this.nowMonth = dater.getMonth() + 1;
    //  console.log(str);
    if (str.length >= 1) {
      // console.log(str[str.length-1]);
      this.imgStr = str[str.length - 1].url;
    }
    this.axios({
      method: "get",
      url: "https://apis.netstart.cn/zhihudaily/stories/latest",
    })
      .then((result) => {
        let res = result.data
        this.Articlelist = res.stories;
        this.Articlelist2 = res.top_stories;
      })
      .catch((err) => {
        console.log("err==>", err);
      });
  },

  methods: {
    // 进我的主页
    myGo() {
      let tokenString = localStorage.getItem("token");
      if (tokenString) {
        this.$router.push({ name: "my" });
      } else {
        this.$toast("请先登录");
        setTimeout(() => {
          this.$router.push({ name: "login", params: { pge: "my" } });
        }, 2000);
      }
    },
    // 进登录页面
    loGingo() {
      this.$router.push({ name: "login" });
    },
    // 懒加载数据
    handleScroll() {
      
      this.oneDayNum = 1000 * 60 * 60 * 24;

      // console.log(this.$refs);
      let clH = this.$refs.mymian.clientHeight;

      let scrollTop =
        window.pageYOffset ||
        document.documentElement.scrollTop ||
        document.body.scrollTop;
      
      let h = document.documentElement.clientHeight;

      let viewHeight = parseInt(scrollTop) + h +30;
     
      if (viewHeight >= clH) {
        // console.log("成功！！！");
        this.oneDay += this.oneDayNum;
        this.oldDay = this.naw - this.oneDay;
        // console.log(this.oneDay);
        let date = new Date(this.oldDay);

        // 日
        let gDate1 = date.getDate();

        // 月
        let gMonth = date.getMonth() + 1;
        // 年
        let gYear = date.getFullYear();

        gMonth = gMonth < 10 ? "0" + gMonth : gMonth;

        this.axios({
          method: "get",
          url: `https://apis.netstart.cn/zhihudaily/stories/before/${gYear}${gMonth}${gDate1}`,
        })
          .then((result) => {
            // console.log('result ==>',result);
            let resArr = result.data.stories;
            for (let i = 0; i < resArr.length; i++) {
              resArr[0].gMonth = gMonth;
              resArr[0].gDate1 = gDate1;
              this.historyList.push(resArr[i]);
            }
          })
          .catch((err) => {
            // console.log("err==>", err);
          });
      }
    },

    // 点击进日报内容
    goCont(item) {
      let id = item.id;

      let tokenString = localStorage.getItem("token");
      if (tokenString) {
        this.$router.push({ name: "contpage", params: { id } });
         localStorage.setItem('id',id)
      } else {
        this.$toast("请先登录");
        setTimeout(() => {
          this.$router.push({ name: "login", params: { id } });
        }, 2000);
      }

      // console.log(item);
    },
  },

  mounted() {
    // 监听滚动条事件
    window.addEventListener("scroll", this.handleScroll, true);

    this.msg = "知乎日报";
  },
};
</script>
 
<style lang="less" scoped>
.mymain {
  width: 100%;
  height: 100%;
  // background-color: #ddd;
  // position: relative;
}
.topbar {
  width: 100%;
  // height: 50px;
  display: flex;
  justify-content: space-between;
  padding: 10px 0;
  // background-color: red;
  background-color: #fff;
  .left {
    // width: 80px;
    display: flex;
    margin-left: 10px;
    div {
      text-align: center;
    }
    .date {
      .date-day {
        font-weight: bold;
      }
    }
    .line {
      width: 1px;
      height: 100%;
      background-color: #ddd;
      margin: 0 10px;
      border-radius: 2px;
    }
    .togreet {
      text-align: center;
      line-height: 30px;
      font-size: 16px;
      font-weight: bold;
    }
  }
  .right {
    width: 30px;
    height: 30px;
    margin-right: 10px;

    > img {
      display: inline-block;
      width: 100%;
      height: 100%;
      border-radius: 50%;
    }
  }
}
.my-swipe .van-swipe-item {
  position: relative;
  .banImg {
    width: 100%;
    height: 250px;
    img {
      display: inline-block;
      width: 100%;
      height: 100%;
    }
  }
  .cont {
    position: absolute;
    //    top: 0;
    bottom: 40px;
    color: rgb(248, 247, 247);
    z-index: 3;
    width: calc(100% - 20px);
    height: 50px;
    padding: 0 10px;
    p {
      margin: 0 0 5px 0;
      //    padding: 0;
      font-size: 16px;
    }
    span {
      color: #ccc;
    }
  }
}

main {
  .list {
    width: 100%;
    display: flex;
    padding: 10px 0;
    .list-cont {
      margin-left: 10px;
      width: 300px;
      padding-top: 5px;
      p {
        margin: 0 0 5px 0;
        width: 270px;
        font-size: 16px;
      }
      span {
        color: rgb(189, 184, 184);
      }
    }
    .list-img {
      width: 60px;
      height: 60px;
      margin-right: 10px;
      img {
        display: inline-block;
        width: 100%;
        height: 100%;
        border-radius: 2px;
      }
    }
  }

  .split-line {
    width: 100%;
    margin-left: 10px;
    span {
      display: inline-block;
    }
    .m-date {
      color: rgb(148, 143, 143);
      margin-right: 10px;
    }
    .m-line {
      width: calc(100% - 70px);
      height: 1px;
      background-color: #ccc;
      margin: 0;
    }
  }
  .list2 {
    width: 100%;
    display: flex;
    padding: 10px 0;
    .list-cont {
      margin-left: 10px;
      width: 300px;
      padding-top: 5px;
      p {
        margin: 0 0 5px 0;
        width: 270px;
        font-size: 16px;
      }
      span {
        color: rgb(189, 184, 184);
      }
    }
    .list-img {
      width: 60px;
      height: 60px;
      margin-right: 10px;
      img {
        display: inline-block;
        width: 100%;
        height: 100%;
        border-radius: 2px;
      }
    }
  }
}
</style>